import { ActionTypes } from "../actions";
import { SchoolActionType } from "../actions/schools";
import {
  PostSchoolInterface,
  SchoolInfoInterface,
  SchoolsInterface,
} from "../shared/interfaces";

export interface SchoolReducerType {
  schools: SchoolsInterface[];
  schoolsInfo: SchoolInfoInterface[];
  schoolPosts: PostSchoolInterface[];
  districtPosts: PostSchoolInterface[];
}

// default state
const initialState: SchoolReducerType = {
  schools: [],
  schoolsInfo: [],
  schoolPosts: [],
  districtPosts: [],
};

export const schoolReducer = (
  state: SchoolReducerType = initialState,
  action: SchoolActionType
) => {
  let index;
  switch (action.type) {
    case ActionTypes.GET_SCHOOLS:
      return {
        ...state,
        schools: action.payload,
      };
    case ActionTypes.CREATE_SCHOOL:
      return {
        ...state,
        schools: [...state.schools, action.payload],
      };
    case ActionTypes.UPDATE_SCHOOL:
      index = state.schools.findIndex(
        (school) => school._id === action.payload._id
      ); //finding index of the item
      if (index >= 0) {
        state.schools[index].school_name = action.payload.school_name;
        state.schools[index].education_category_id =
          action.payload.education_category_id;
        state.schools[index].district_id = action.payload.district_id;
      }
      return state;
    case ActionTypes.DELETE_SCHOOL:
      let data = state.schools.filter(
        (element) => element._id !== action.payload
      );
      return {
        ...state,
        schools: data,
      };
    case ActionTypes.GET_SCHOOLS_INFO:
      return {
        ...state,
        schoolsInfo: action.payload,
      };
    case ActionTypes.FETCH_SCHOOL_POSTS:
      return {
        ...state,
        schoolPosts: action.payload,
      };
    case ActionTypes.CREATE_SCHOOL_POST:
      return {
        ...state,
        schoolPosts: [...state.schoolPosts, action.payload],
      };
    case ActionTypes.UPDATE_SCHOOL_POST:
      let newData: PostSchoolInterface[] = [...state.schoolPosts];
      index = state.schoolPosts.findIndex(
        (post) =>
          post.requested_post_id === action.payload.data.requested_post_id
      ); //finding index of the item
      if (index >= 0) {
        if (action.payload.TYPE === "DISTRICT") {
          newData[index].confirmed_posts = action.payload.data.confirmed_posts;
          newData[index].approval_date = action.payload.data.approval_date;
          newData[index].confirmed_by = action.payload.data.confirmed_by;
        } else if (action.payload.TYPE === "RECEIVED") {
          newData[index].received_posts = action.payload.data.received_posts;
        } else {
          newData[index].description = action.payload.data.description;
          newData[index].female_number = action.payload.data.female_number;
          newData[index].male_number = action.payload.data.male_number;
          newData[index].occupied_posts = action.payload.data.occupied_posts;
          newData[index].request_date = action.payload.data.request_date;
          newData[index].requested_by = action.payload.data.requested_by;
          newData[index].requested_post_id =
            action.payload.data.requested_post_id;
        }
        // state.schoolPosts[index].post_at_school_status =
        //   action.payload.post_at_school_status;
        // state.schoolPosts[index].confirmed_posts =
        //   action.payload.data.confirmed_posts;
      }
      return { ...state, schoolPosts: newData };
    case ActionTypes.FETCH_DISTRICT_POSTS:
      return {
        ...state,
        districtPosts: action.payload,
      };
    default:
      return state;
  }
};
