import {
  ActionTypes,
  AdminExamReportActionType,
  ReportGetCandidatePerformanceFilterTypes,
  ReportGetCandidatePerformanceSortTypes,
} from "../actions";
import { examProgressStateInterface } from "../containers/Admin/ExamReport/DoneExamList";
import {
  CandidatesFailedSucceeded,
  ExamDoneAndRemainingByPeriodInterface,
  ExamsParcentageSuccessFailedRateByPeriodInterface,
} from "../interfaces/reports";

export interface ExamReportReducerType {
  ExamPerformance: ExamsParcentageSuccessFailedRateByPeriodInterface[] | null;
  DoneExam: ExamDoneAndRemainingByPeriodInterface[] | null;
  CandidatesExamPerformance: CandidatesFailedSucceeded[] | null;
  CandidatePerformanceFilterType: ReportGetCandidatePerformanceFilterTypes | null;
  CandidatePerformanceCurrentPage: { skip: number; prevLimit: number } | null;
  CandidatePerformanceSortType: ReportGetCandidatePerformanceSortTypes | null;
  CandidatePerformanceOrderType: "asc" | "desc" | null;
  reportDistrictId: { district_id: string; district_name: string } | null;
  reportCourseId: examProgressStateInterface | null;
}

// default state
const initialState: ExamReportReducerType = {
  ExamPerformance: null,
  DoneExam: null,
  CandidatesExamPerformance: null,
  CandidatePerformanceFilterType: null,
  CandidatePerformanceCurrentPage: null,
  CandidatePerformanceSortType: null,
  CandidatePerformanceOrderType: null,
  reportDistrictId: null,
  reportCourseId: null,
};

export const examReportReducer = (
  state: ExamReportReducerType = initialState,
  action: AdminExamReportActionType
) => {
  switch (action.type) {
    case ActionTypes.GET_SUCCESSED_FAILED_EXAMS:
      return {
        ...state,
        ExamPerformance: action.payload,
      };
    case ActionTypes.GET_DONE_EXAMS:
      return {
        ...state,
        DoneExam: action.payload,
      };
    case ActionTypes.GET_CANDIDATE_EXAMS_PERFOMRANCE:
      return {
        ...state,
        CandidatesExamPerformance: action.payload,
      };
    case ActionTypes.SET_CANDIDATE_EXAMS_PERFOMRANCE_FILTER_TYPE:
      return {
        ...state,
        CandidatePerformanceFilterType: action.payload,
      };
    case ActionTypes.SET_CANDIDATE_EXAMS_PERFOMRANCE_CURRENT_PAGE:
      return {
        ...state,
        CandidatePerformanceCurrentPage: action.payload,
      };
    case ActionTypes.SET_CANDIDATE_EXAMS_PERFOMRANCE_SORT_TYPE:
      return {
        ...state,
        CandidatePerformanceSortType: action.payload,
      };
    case ActionTypes.SET_CANDIDATE_EXAMS_PERFOMRANCE_ORDER:
      return {
        ...state,
        CandidatePerformanceOrderType: action.payload,
      };
    case ActionTypes.REPORT_SET_DISTRICT_ID:
      return {
        ...state,
        reportDistrictId: action.payload,
      };
    case ActionTypes.REPORT_SET_COURSE_ID:
      return {
        ...state,
        reportCourseId: action.payload,
      };
    default:
      return state;
  }
};
