import React, { Fragment, ReactNode } from "react";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import {
  EventInterface,
  Events,
  EventsLabels,
  // CandidateProctoringInterface,
  // OnlineProctoringRoom,
  // LiveServer__Candidate,
  LiveServer__Candidate_Details,
} from "../../interfaces";
import InformationModal from "../../components/InformationModal/InformationModal";

import {
  Auth,
  Socket,
  Proctoring,
  FC_MeStartStopExam,
  FC_CandidateExamEvent,
  FC_BlockExam,
  FC_SaveEvent,
  FC_AddEvent,
  FC_LoadUserEvents,
  FC_LoadUserEvents_Interface,
  FC_UpdateExamToStartEvent,
  FC_RoomDetailsCandidate,
  FC_CandidateDoneStopEvents,
} from "../../actions";
import {
  MdFullscreen,
  MdFullscreenExit,
  MdReport,
  MdOutlineWarning,
  MdDoDisturb,
  MdRefresh,
} from "react-icons/md";
import { Link } from "react-router-dom";

// props for the component
interface ProctoringCandidateOnlineProps {
  socket: Socket;
  // eventDefiner: Function;
  auth: Auth;
  proctoring: Proctoring;
  // FC_AddMyDataToProctoring: Function;
  FC_MeStartStopExam: Function;
  FC_CandidateExamEvent: Function;
  children: ReactNode;
  startExam: Function;
  stopExam: Function;
  candidateStopExam: Function;
  restartExam: Function;
  FC_BlockExam: Function;
  FC_AddEvent: Function;
  FC_SaveEvent: Function;
  examAlreadyDone: Function;
  room_id: string;
  exam_id: string;
  position_id: string;
  application_session_id: string;
  FC_LoadUserEvents: Function;
  FC_UpdateExamToStartEvent: Function;
  removeClient: Function;
  FC_RoomDetailsCandidate: Function;
  FC_CandidateDoneStopEvents: Function;
  candidateContinueExam: Function;
  joinedRoom?: Function;
  changeTime?: Function;
}

interface AppState {
  request_fullScreen: boolean;
  exit_fullScreen: boolean;
  warning: any;
  inform_modal: {
    title: string;
    description: string;
    active: boolean;
  };
  error: {
    target: string;
    msg: string;
  };
  room_error: boolean;
  group_id: string;
  start_time: string;
  added_minutes: number;
  duration: number;
}

let joined_room = false;

class _ProctoringCandidateOnline extends React.Component<
  ProctoringCandidateOnlineProps,
  AppState
> {
  constructor(props: ProctoringCandidateOnlineProps) {
    super(props);

    this.state = {
      request_fullScreen: this.props.proctoring.exam.start_exam,
      exit_fullScreen: false,
      warning: null,
      inform_modal: {
        title: "",
        description: "",
        active: false,
      },
      error: {
        target: "",
        msg: "",
      },
      room_error: false,
      group_id: "",
      added_minutes: 0,
      start_time: "",
      duration: 0,
    };
  }

  componentDidMount() {
    if (joined_room === false) {
      this.props.socket.socket.emit(
        Events.JOIN_ROOM,
        {
          room_id: this.props.room_id,
          user_id: this.props.auth.user.user_id,
          peer_id: "",
          supper: false,
        },
        (data: {
          data: LiveServer__Candidate_Details;
          error: boolean;
          msg: string;
        }) => {
          if (data.error === false) {
            // Load room events
            this.setState({
              room_error: false,
              error: {
                target: "",
                msg: "",
              },
            });

            // Add a group name
            this.setState({
              group_id: data.data.group_id,
              added_minutes: data.data.user.added_minutes,
              start_time:
                data.data.start_time === null ? "" : data.data.start_time,
              duration: data.data.duration,
            });

            if (this.props.joinedRoom) this.props.joinedRoom(data.data);

            //* Check if the candidate is done with the exam or if the exam
            if (data.data.exam_done === true || data.data.user.done === true) {
              this.props.FC_CandidateDoneStopEvents(true);
              this.props.examAlreadyDone();
            } else if (data.data.start_exam === true) {
              //* Start exam

              this.examStart(true);
              console.log({ cand_timer: data.data.candidate_timer });

              if (this.props.changeTime)
                this.props.changeTime(data.data.candidate_timer);

              //* Stop candidate
              if (data.data.user.stopped === true)
                this.props.FC_BlockExam(true);
            }

            //* Save room info
            this.props.FC_RoomDetailsCandidate(data.data);

            //* Emit all the events =================================

            // Listen events
            this.props.socket.socket.on(
              Events.START_EXAM_CLIENT,
              (
                event: EventInterface,
                exam_time: { start_time: string; duration: number }
              ) => {
                this.setState({
                  start_time: exam_time.start_time,
                  duration: exam_time.duration,
                });
                if (this.props.changeTime)
                  this.props.changeTime({
                    duration: exam_time.duration,
                    start_time: exam_time.start_time,
                    added_minutes: 0,
                  });
                this.examStart(true);
                this.props.FC_AddEvent(event);
              }
            );

            // Listen events
            this.props.socket.socket.on(
              Events.CAND_REMOVE_EXAM_CLIENT,
              (event: EventInterface) => {
                this.props.FC_AddEvent(event);
                this.props.removeClient();
              }
            );

            // Listen events
            this.props.socket.socket.on(
              Events.STOP_EXAM_CLIENT,
              (event: EventInterface) => {
                this.examStart(false);
                this.props.FC_AddEvent(event);
              }
            );

            // * WARNING CANDIDATE
            this.props.socket.socket.on(
              Events.CAND_WARNING_EXAM_CLIENT,
              (event: EventInterface) => {
                this.setState((prevState: AppState) => ({
                  warning: true,
                  inform_modal: {
                    title: "WARNING",
                    description: event.description,
                    active: true,
                  },
                }));
                this.props.FC_AddEvent(event);
              }
            );

            // * STOP EXAM
            this.props.socket.socket.on(
              Events.CAND_STOP_EXAM_CLIENT,
              (event: EventInterface) => {
                if (event.user_id === this.props.auth.user.user_id) {
                  this.setState((prevState: AppState) => ({
                    inform_modal: {
                      title: EventsLabels.STOP_EXAM,
                      description: event.description,
                      active: true,
                    },
                  }));
                  this.props.FC_BlockExam(true);
                  this.props.candidateStopExam(
                    `${EventsLabels.STOP_EXAM}: ${event.description}`
                  );
                  // Add event to the reducer
                  this.props.FC_AddEvent(event);
                }
              }
            );

            // * CONTINUE EXAM
            this.props.socket.socket.on(
              Events.CAND_CONTINUE_EXAM_CLIENT,
              (event: EventInterface) => {
                if (
                  event.user_id === this.props.auth.user.user_id &&
                  this.props.proctoring.me.stopped === true
                ) {
                  this.setState((prevState: AppState) => ({
                    inform_modal: {
                      title: EventsLabels.CONTINUE_EXAM,
                      description: event.description,
                      active: true,
                    },
                  }));

                  this.props.FC_BlockExam(false);
                  this.props.candidateContinueExam(
                    `${EventsLabels.CONTINUE_EXAM}: ${event.description}`
                  );

                  // Add event to the reducer
                  this.props.FC_AddEvent(event);
                }
              }
            );

            // * CONTINUE EXAM
            this.props.socket.socket.on(
              Events.CAND_RESTART_EXAM_CLIENT,
              (event: EventInterface) => {
                if (event.user_id === this.props.auth.user.user_id) {
                  this.setState((prevState: AppState) => ({
                    inform_modal: {
                      title: EventsLabels.RESTART_EXAM,
                      description: event.description,
                      active: true,
                    },
                  }));
                  this.props.FC_BlockExam(false);

                  // Add event to the reducer
                  this.props.FC_AddEvent(event);
                  // * Restart Function
                  this.props.restartExam(
                    `${EventsLabels.RESTART_EXAM}: ${event.description}`
                  );
                }
              }
            );

            // * CONTINUE EXAM
            this.props.socket.socket.on(
              Events.ADD_TIME_TO_CANDIDATE_CLIENT,
              (
                {
                  start_time,
                  duration,
                  minutes,
                }: {
                  start_time: string;
                  duration: number;
                  minutes: number;
                },
                event: EventInterface
              ) => {
                if (event.user_id === this.props.auth.user.user_id) {
                  if (this.props.changeTime)
                    this.props.changeTime({
                      start_time,
                      duration,
                      minutes,
                    });

                  this.props.FC_BlockExam(false);
                  this.props.candidateContinueExam(
                    `${EventsLabels.CONTINUE_EXAM}: ${event.description}`
                  );

                  // Add the event to the reducer
                  this.props.FC_AddEvent(event);
                }
              }
            );

            //* =====================================================
            // load Events
            this.props.FC_LoadUserEvents(
              {
                application_session_id: this.props.application_session_id,
                exam_id: this.props.exam_id,
                room_id: this.props.room_id,
                user_id: this.props.auth.user.user_id,
              } as FC_LoadUserEvents_Interface,
              (status: boolean, error: string) => {}
            );
          } else {
            this.setState({
              room_error: true,
              error: {
                target: "room_error",
                msg: data.msg,
              },
            });
          }
        }
      );
      joined_room = true;
    }

    //** EVENTS BROWSER */
    // document.addEventListener("mouseenter", (e) => {
    //   this.eventBrodcaster(
    //     EventsLabels.LEAVE_BROWSER,
    //     "The candidate is try to leave the browser"
    //   );
    // });

    // document.addEventListener("mouseleave", (event) =>
    //   this.eventBrodcaster(
    //     EventsLabels.LEAVE_BROWSER,
    //     "The candidate is try to leave the browser"
    //   )
    // );

    document.addEventListener("copy", (event) => {
      if (
        this.props.proctoring.exam.start_exam === true &&
        this.props.proctoring.exam.exam_done === false
      ) {
        event.preventDefault();
        // this.eventBrodcaster(
        //   "COPY TEXT",
        //   "The candidate is try to copy the text"
        // );
      }
    });

    document.addEventListener("paste", (event) => {
      if (
        this.props.proctoring.exam.start_exam === true &&
        this.props.proctoring.exam.exam_done === false
      ) {
        event.preventDefault();
        // this.eventBrodcaster("TEXT PASTE", "pasted a text");
      }
    });

    document.addEventListener("visibilitychange", (event) =>
      this.eventBrodcaster(EventsLabels.ZOOM_OUT, "Changing the visibility")
    );

    // ! ERROR: causing to stop the candidate once they click on a modal into the browser
    // document.addEventListener("focusout", (event) =>
    //   this.eventBrodcaster(
    //     EventsLabels.LEAVE_BROWSER,
    //     "The candidate is try to leave the browser"
    //   )
    // );

    window.addEventListener("blur", () =>
      this.eventBrodcaster(
        EventsLabels.LEAVE_BROWSER,
        "The candidate is try to leave the browser"
      )
    );

    // prevent the right-click
    document.addEventListener("contextmenu", (event) => {
      if (
        this.props.proctoring.exam.start_exam === true &&
        this.props.proctoring.exam.exam_done === false
      ) {
        event.preventDefault();
      }
    });
  }

  componentWillUnmount = () => {
    this.props.socket.socket.off(Events.START_EXAM_CLIENT);
    this.props.socket.socket.off(Events.STOP_EXAM_CLIENT);
    this.props.socket.socket.off(Events.CAND_CONTINUE_EXAM_CLIENT);
    this.props.socket.socket.off(Events.CAND_RESTART_EXAM_CLIENT);
    this.props.socket.socket.off(Events.CAND_WARNING_EXAM_CLIENT);
    this.props.socket.socket.off(Events.CAND_STOP_EXAM_CLIENT);
  };

  // ***************************************************************************

  /**
   * @description define whether the exam has started or not
   * @param state boolean
   */
  examStart = (state: boolean) => {
    if (state === true) {
      // start exam
      this.setState({
        request_fullScreen: true,
        exit_fullScreen: false,
      });

      this.props.FC_MeStartStopExam(true);
      this.props.startExam(
        this.state.start_time,
        this.state.duration,
        this.state.added_minutes
      );
    } else {
      // stop exam mode
      this.setState({
        request_fullScreen: false,
        exit_fullScreen: true,
      });

      this.props.FC_MeStartStopExam(false);
      this.props.stopExam();
    }
  };

  closeInfoModal = () =>
    this.setState({
      inform_modal: {
        active: false,
        description: "",
        title: "",
      },
    });

  /**
   * @description Request the fullscreen
   */
  requestFullScreen = () => {
    if (!document.fullscreenElement) {
      document.documentElement.requestFullscreen();
    }
    this.setState({
      request_fullScreen: false,
      exit_fullScreen: false,
    });
  };

  /**
   * @description Exist the fullscreen
   */
  exitFullScreen = () => {
    this.setState({
      request_fullScreen: false,
      exit_fullScreen: false,
    });
    if (document.fullscreenElement) {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      }
    }
  };

  /**
   * @description get all the events and send them to the supervisor
   * @param event
   */
  eventBrodcaster = (type: string, description: string) => {
    if (
      this.props.proctoring.exam.start_exam === true &&
      this.props.proctoring.exam.exam_done === false
    ) {
      // Add event to the reducer
      this.props.candidateStopExam(`${type}: ${description}`);
      this.props.FC_BlockExam(true);

      this.props.FC_SaveEvent(
        {
          application_session_id: this.props.application_session_id,
          description: description,
          done_by: "System",
          event_type_id: type,
          exam_id: this.props.exam_id, //this.props.proctoring.exam.
          position_id: this.props.position_id,
          room_id: this.props.room_id,
          time: new Date().toString(),
          user_id: this.props.auth.user.user_id,
          group_id: this.state.group_id,
        } as EventInterface,
        (status: boolean, error: string, event: EventInterface | null) => {
          if (status === true) {
            // send the event on the server
            this.props.socket.socket.emit(Events.CAND_EXAM_EVENT_SERVER, event);
          } else {
            this.setState({ error: { target: "main", msg: error } });
          }
        }
      );
    }
  };

  joinRoomOnline = () => window.location.reload();

  render() {
    //* once there is an error
    if (
      this.state.room_error === true &&
      this.state.error.target === "room_error"
    ) {
      return (
        <div className="h-screen -mt-20 flex items-center self-center">
          <div className="mx-auto bg-white rounded-lg shadow text-center overflow-hidden animate__animated animate__zoomIn animate__faster">
            <div className="py-4 px-6 ">
              <MdOutlineWarning className="mx-auto text-8xl text-blue-900" />
              <h2 className="text-2xl font-bold mb-1 text-blue-900">
                {this.state.error.msg}
              </h2>
              <p className="text-gray-600">
                You can't join the room right now, please try again later <br />{" "}
                or ask the administrator or the supervisor
              </p>
            </div>
            <div className="flex text-gray-500 border-t">
              <button
                onClick={this.joinRoomOnline}
                className="p-3 flex-1 hover:bg-green-200 hover:text-green-800 font-bold"
              >
                <MdRefresh className="inline" /> Try again
              </button>
              <Link
                to="/profile"
                className="p-3 flex-1 hover:bg-red-200 hover:text-red-800 font-bold"
              >
                <MdDoDisturb className="inline" /> Leave room
              </Link>
            </div>
          </div>
        </div>
      );
    }
    //* candidate stopped screen
    if (
      this.props.proctoring.me.stopped === true &&
      this.props.proctoring.exam.start_exam === true &&
      this.props.proctoring.exam.exam_done === false &&
      this.props.proctoring.me.done === false
    ) {
      let lastEvent: EventInterface | null = null;
      let events_list = this.props.proctoring.events;
      if (events_list.length >= 1) {
        lastEvent = events_list[events_list.length - 1];
      }
      return (
        <div className="p-5 mx-auto mt-6 text-center bg-red-100 max-w-lg max-w-2/3 w-full rounded-md animate__bounceIn">
          <MdReport className="text-9xl text-red-900 inline" />
          <h1 className="text-2xl text-red-900 font-bold">
            You have been stopped <br /> to continue the exam
          </h1>

          {lastEvent !== null && (
            <Fragment>
              <div className="mt-2">
                <h5 className="text-lg  text-red-800 inline-block">
                  You violated the rule of:
                </h5>
                <h2 className="text-md text-white font-bold bg-red-600 border-4 py-0.5 px-3 rounded-full inline-block ml-2 mx-auto">
                  {lastEvent?.event_type_id}
                </h2>
              </div>

              <h3 className="text-md text-red-800 mt-3 font-bold">
                {lastEvent?.description}
              </h3>
            </Fragment>
          )}

          <div className="mt-5 border-t border-red-900 border-opacity-50 text-sm text-red-900 text-opacity-80 pt-3 mx-5">
            <b>The exam will be saved from where you left off,</b> <br />
            You can explain the situation to the supervisor <br /> If your
            reasons are clear you will be given another chance
          </div>
        </div>
      );
    }

    //* Request for the full screen
    if (this.state.request_fullScreen === true) {
      return (
        <div className="bg-green-100 w-auto mt-6 rounded-md mx-auto text-center p-5 max-w-sm animate__bounceIn">
          <div className="">
            <MdFullscreenExit className="text-9xl inline text-green-600 opacity-50" />
            <h1 className="text-3xl text-green-500 font-bold">
              Go Full-Screen mode{" "}
            </h1>
            <p className="mt-2 text-gray-700">
              You need to go in <b>Full-screen mode</b>, in order to get a good
              experience (Important)
            </p>
            <button
              className=" bg-green-600 hover:bg-green-800 text-white p-3 font-bold text-md rounded-md w-full mt-3"
              onClick={() => this.requestFullScreen()}
            >
              Yes, go full-screen
            </button>
          </div>
        </div>
      );
    }

    //* Exit for the full screen
    if (this.state.exit_fullScreen === true) {
      return (
        <div className="bg-blue-100 w-auto mt-6 rounded-md mx-auto text-center p-5 max-w-sm animate__bounceIn">
          <div className="">
            <MdFullscreen className="text-9xl inline text-blue-600 opacity-50" />
            <h1 className="text-3xl text-blue-600 font-bold">
              Quit Full-Screen mode
            </h1>
            <p className="mt-2 text-gray-700">
              Exam is completed, you can quit the <b>Fullscreen mode</b>
              (optional)
            </p>
            <button
              className=" bg-blue-600 text-white p-3 font-bold text-md rounded-md w-full mt-3"
              onClick={() => this.exitFullScreen()}
            >
              Exit full-screen
            </button>
          </div>
        </div>
      );
    }

    return (
      <Fragment>
        {this.state.inform_modal.active === true && (
          <InformationModal
            title={this.state.inform_modal.title}
            description={this.state.inform_modal.description}
            close={this.closeInfoModal}
          />
        )}

        {this.props.children}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  socket,
  auth,
  proctoring,
}: StoreState): { socket: Socket; proctoring: Proctoring; auth: Auth } => {
  return { socket: socket, auth, proctoring };
};

export const ProctoringCandidateOnline = connect(mapStateToProps, {
  // FC_AddMyDataToProctoring,
  FC_MeStartStopExam,
  FC_CandidateExamEvent,
  FC_BlockExam,
  FC_SaveEvent,
  FC_AddEvent,
  FC_LoadUserEvents,
  FC_UpdateExamToStartEvent,
  FC_RoomDetailsCandidate,
  FC_CandidateDoneStopEvents,
})(_ProctoringCandidateOnline);
