import {
  Proctoring,
  Action,
  ActionTypes,
  ActiveCandidateLiveServer,
} from "../actions";
import {
  CandidateProctoringInterface,
  CandidateProctoringInterfaceAPI,
  ProctoringExam,
  RoomServer,
} from "../interfaces";
// default state

const defaultCandidateEmpty = {
  course_code: "",
  course_id: "",
  course_name: "",
  district: "",
  education_institute_id: "",
  email: "",
  fname: "No first name",
  lname: "No last name",
} as CandidateProctoringInterfaceAPI;

const defaultMe = {
  active: false,
  email: "",
  fname: "",
  lname: "",
  stopped: false,
  user_id: "",
  muted: false,
  peer: "",
  peer_id: "",
  ref: "",
  course_code: "",
  course_id: "",
  course_name: "",
  district: "",
  education_institute_id: "",
  n_id: "",
  phone_number: "",
  position_id: "",
  position_title: "",
  school_level_id: "",
  done: false,
  joined: true,
  removed: false,
  room_id: "",
  supervisor: false,
  seat_number: 0,
  added_minutes: 0,
  start_added_time: "",
} as CandidateProctoringInterface;

const defaultRoom = {
  loaded: false,
  center_id: "",
  center_name: "",
  district_id: "",
  district_name: "",
  province_id: "",
  province_name: "",
  room_id: "",
  room_number: "",
} as RoomServer;

const defaultExam = {
  video_support: false,
  start_exam: false,
  start_time: "",
  end_time: "",
  exam_done: false,
  period_id: "",
  exam_status: "",
  exam_date: "",
  duration: 0,
  position_id: "",
  position_title: "",
  course_id: "",
  course_code: "",
  course_name: "",
  room_id: "",
  exam_id: "",
} as ProctoringExam;

const defaultState = {
  events: [],
  room: defaultRoom,
  all_candidates: [],
  candidates: [],
  candidate_loaded: false,
  me: defaultMe,
  room_exams: [],
  room_exams_loaded: false,
  exam: defaultExam,
  exams_list: {
    exams: [],
    loaded: false,
  },
  room_not_found: false,
  error: "",
  room_loaded: false,
} as Proctoring;

/**
 * @param state
 * @param action
 * @returns
 */
export const proctoringReducer = (
  state: Proctoring = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.ADD_ROOM_DETAILS:
      return {
        ...state,
        candidates: action.payload.candidates,
        room: { ...state.room, ...action.payload.room },
      } as Proctoring;
    case ActionTypes.ADD_ACTIVE_USER_TO_ROOM:
      return {
        ...state,
        candidates: state.candidates.map(
          (cand: CandidateProctoringInterface) => {
            // find the current candidate to the list of active candidates
            let candExists = action.payload.find(
              (itm: ActiveCandidateLiveServer) => itm.user_id === cand.user_id
            );
            return {
              ...cand,
              active: candExists === undefined ? false : true,
            } as CandidateProctoringInterface;
          }
        ),
      } as Proctoring;

    case ActionTypes.ADD_ONE_ACTIVE_USER_TO_ROOM:
      return {
        ...state,
        candidates: state.candidates.map((itm: CandidateProctoringInterface) =>
          itm.user_id === action.payload.user_id
            ? {
                ...itm,
                stopped: action.payload.stopped,
                done: action.payload.done,
                active: true,
              }
            : itm
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_OFFLINE:
      return {
        ...state,
        candidates: state.candidates.map((itm: CandidateProctoringInterface) =>
          itm.user_id === action.payload
            ? { ...itm, active: false, stopped: true }
            : itm
        ),
      } as Proctoring;
    case ActionTypes.EXAM_START_STOP:
      let data_exam = {
        ...state.exam,
        start_exam: action.payload.status,
      };
      if (action.payload.start_time !== "" && action.payload.duration !== 0) {
        data_exam = {
          ...data_exam,
          start_time: action.payload.start_time,
          duration: action.payload.duration,
        };
      }
      return {
        ...state,
        exam: data_exam,
      } as Proctoring;
    case ActionTypes.CANDIDATE_CONTINUE_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload ? { ...cand, stopped: false } : cand
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_STOP_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload ? { ...cand, stopped: true } : cand
        ),
      } as Proctoring;

    case ActionTypes.CANDIDATE_REMOVE_EXAM:
      return {
        ...state,
        candidates: state.candidates.filter(
          (cand) => cand.user_id !== action.payload
        ),
      } as Proctoring;

    case ActionTypes.CANDIDATE_EXAM_EVENT:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload.user_id
            ? { ...cand, stopped: true }
            : cand
        ),
        events: [...state.events, action.payload],
      } as Proctoring;
    case ActionTypes.ADD_MY_DATA_TO_PROCTORING:
      return {
        ...state,
        me: action.payload as CandidateProctoringInterface,
      } as Proctoring;
    case ActionTypes.ME_START_STOP_EXAM:
      return {
        ...state,
        room: {
          ...state.room,
          start_exam: action.payload,
          exam_done: !action.payload,
        },
        exam: {
          ...state.exam,
          start_exam: action.payload,
          exam_done: !action.payload,
        },
      } as Proctoring;
    case ActionTypes.BLOCK_EXAM:
      return {
        ...state,
        me: {
          ...state.me,
          stopped: action.payload,
        },
      } as Proctoring;
    case ActionTypes.LOAD_ROOM_DETAILS:
      return {
        ...state,
        room: {
          ...action.payload.room,
          loaded: true,
        },
        room_exams: action.payload.room_exams,
        room_exams_loaded: true,
      };
    case ActionTypes.PROCTORING_GET_EXAMS_LIST:
      return {
        ...state,
        exams_list: {
          ...state.exams_list,
          loaded: true,
          exams: action.payload,
        },
      };
    case ActionTypes.LOAD_EXAM_DETAILS_TO_ROOM_PROCTORING:
      return {
        ...state,
        exam: action.payload,
      } as Proctoring;
    case ActionTypes.CLEAR_EXAM_DETAILS_TO_ROOM_PROCTORING:
      return {
        ...state,
        exam: defaultState.exam,
        candidate_loaded: false,
        candidates: [],
      } as Proctoring;
    case ActionTypes.ADD_CANDIDATES_ROOM_PROCTORING:
      return {
        ...state,
        candidate_loaded: true,
        all_candidates: action.payload,
        candidates: [],
      } as Proctoring;
    case ActionTypes.PROCTORING_ADD_EVENT:
      return {
        ...state,
        events: [action.payload, ...state.events],
      } as Proctoring;
    case ActionTypes.PROCTORING_LOAD_EVENTS:
      return {
        ...state,
        events: action.payload,
      } as Proctoring;
    case ActionTypes.UPDATE_THE_EXAM_START_STATUS:
      return {
        ...state,
        room: {
          ...state.room,
          start_exam: true,
          exam_done: false,
        },
        exam: {
          ...state.exam,
          start_exam: true,
          exam_done: false,
          start_time: action.payload,
        },
      } as Proctoring;
    case ActionTypes.ROOM_NOT_FOUND_ONLINE:
      return {
        ...state,
        room_not_found: true,
        error: action.payload,
      } as Proctoring;
    case ActionTypes.ROOM_DETAILS_ONLINE:
      return {
        ...state,
        room_loaded: true,
        room_not_found: false,
        candidates: action.payload.candidates
          .filter((cand) => cand.supervisor === false)
          .map((candidate) => {
            let findCandidateDetails:
              | CandidateProctoringInterfaceAPI
              | undefined = state.all_candidates.find(
              (itm) => itm.user_id === candidate.user_id
            );

            if (findCandidateDetails === undefined) {
              findCandidateDetails = defaultCandidateEmpty;
            }

            return {
              ...findCandidateDetails,
              ...candidate,
            };
          }),
        room: {
          ...state.room,
          group_id: action.payload.group_id,
        },
        exam: {
          ...state.exam,
          exam_done: action.payload.exam_done,
          start_exam: action.payload.start_exam,
          start_time: action.payload.start_time,
          duration: action.payload.duration,
        },
      } as Proctoring;
    case ActionTypes.ROOM_DETAILS_CANDIDATE_ONLINE:
      return {
        ...state,
        me: {
          ...state.me,
          ...action.payload.user,
          active: true,
        },
        room: {
          ...state.room,
          group_id: action.payload.group_id,
        },
        exam: {
          ...state.exam,
          start_exam: action.payload.start_exam,
          start_time: action.payload.start_time,
          exam_done: action.payload.exam_done,
        },
      } as Proctoring;
    case ActionTypes.CANDIDATE_DONE_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand) =>
          cand.user_id === action.payload ? { ...cand, done: true } : cand
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_STOP_EVENTS:
      return {
        ...state,
        exam: {
          ...state.exam,
          exam_done: action.payload,
        },
        me: {
          ...state.me,
          done: action.payload,
        },
      } as Proctoring;
    case ActionTypes.EXAM_DONE:
      return {
        ...state,
        exam: {
          ...state.exam,
          exam_done: action.payload,
        },
      } as Proctoring;
    case ActionTypes.ADD_TIME_TO_CANDIDATE:
      return {
        ...state,
        candidates: state.candidates.map((candid) =>
          candid.user_id === action.payload.user_id
            ? {
                ...candid,
                added_minutes: action.payload.minutes,
                stopped: false,
                start_added_time:
                  state.exam.exam_done === true ? new Date().toString() : "",
              }
            : candid
        ),
      } as Proctoring;
    default:
      return state;
  }
};
