import axios from "axios";
import { Dispatch } from "redux";
import { examProgressStateInterface } from "../containers/Admin/ExamReport/DoneExamList";
import {
  CandidatesFailedSucceeded,
  ExamDoneAndRemainingByPeriodInterface,
  ExamsParcentageSuccessFailedRateByPeriodInterface,
} from "../interfaces/reports";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { ActionTypes } from "./types";

export type FilterParamType = {
  order?: "asc" | "desc";
  sortBy?: ReportGetCandidatePerformanceSortTypes;
  limit?: number;
  skip?: number;
  filter?: {
    district_id?: string;
    course?: string;
    period?: string;
    center_id?: string;
    room_id?: string;
  };
};

export enum ReportGetCandidatePerformanceFilterTypes {
  DISTRICT = "district_id",
  COURSE = "course",
  PERIOD = "period",
  CENTER = "center_id",
  ROOM = "room_id",
}

export enum ReportGetCandidatePerformanceSortTypes {
  MARKS = "user_total_marks",
  DATE = "createdAt",
  STATUS = "status",
  EXAM = "exam_id",
  COURSE = "course",
}

export interface GetSuccessAndFailedExamAction {
  type: ActionTypes.GET_SUCCESSED_FAILED_EXAMS;
  payload: ExamsParcentageSuccessFailedRateByPeriodInterface[];
}

export interface GetDoneExamAction {
  type: ActionTypes.GET_DONE_EXAMS;
  payload: ExamDoneAndRemainingByPeriodInterface[];
}

export interface GetCandidatesExamPerformanceAction {
  type: ActionTypes.GET_CANDIDATE_EXAMS_PERFOMRANCE;
  payload: CandidatesFailedSucceeded[];
}

export interface SetGetCandidatesExamPerformanceFilterTypeAction {
  type: ActionTypes.SET_CANDIDATE_EXAMS_PERFOMRANCE_FILTER_TYPE;
  payload: ReportGetCandidatePerformanceFilterTypes;
}

export interface SetGetCandidatesExamPerformanceCurrentPageAction {
  type: ActionTypes.SET_CANDIDATE_EXAMS_PERFOMRANCE_CURRENT_PAGE;
  payload: { skip: number; prevLimit: number };
}

export interface SetGetCandidatesExamPerformanceSortTypeAction {
  type: ActionTypes.SET_CANDIDATE_EXAMS_PERFOMRANCE_SORT_TYPE;
  payload: ReportGetCandidatePerformanceSortTypes;
}

export interface SetGetCandidatesExamPerformanceOrderTypeAction {
  type: ActionTypes.SET_CANDIDATE_EXAMS_PERFOMRANCE_ORDER;
  payload: "asc" | "desc";
}

export interface SetReportDistrictIdAction {
  type: ActionTypes.REPORT_SET_DISTRICT_ID;
  payload: { district_id: string; district_name: string } | null;
}

export interface SetReportCourseIdAction {
  type: ActionTypes.REPORT_SET_COURSE_ID;
  payload: examProgressStateInterface | null;
}

export type AdminExamReportActionType =
  | GetSuccessAndFailedExamAction
  | GetDoneExamAction
  | GetCandidatesExamPerformanceAction
  | SetGetCandidatesExamPerformanceFilterTypeAction
  | SetGetCandidatesExamPerformanceCurrentPageAction
  | SetGetCandidatesExamPerformanceSortTypeAction
  | SetGetCandidatesExamPerformanceOrderTypeAction
  | SetReportDistrictIdAction
  | SetReportCourseIdAction;

/**
 * * ****************************** ACTIONS *****************************
 */

/**
 * GET ALL SUCCESSED AND FAILED EXAMS
 * @param period_id
 * @param callback
 * @returns
 */
export const loadPerfomranceExam = (period_id: string, callback: Function) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<
        ExamsParcentageSuccessFailedRateByPeriodInterface[]
      >(`${API.DO_EXAM}/reports/exams/${period_id}`);
      dispatch<GetSuccessAndFailedExamAction>({
        type: ActionTypes.GET_SUCCESSED_FAILED_EXAMS,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.error({ code_error: error });
      callback(false, errorToText(error));
    }
  };
};

/**
 * GET ALL DONE EXAM
 * @param period_id
 * @param callback
 * @returns
 */
export const loadDoneExam = (period_id: string, callback: Function) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<ExamDoneAndRemainingByPeriodInterface[]>(
        `${API.DO_EXAM}/reports/period/${period_id}`
      );
      dispatch<GetDoneExamAction>({
        type: ActionTypes.GET_DONE_EXAMS,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.error({ code_error: error });
      callback(false, errorToText(error));
    }
  };
};

/**
 * GET CANDIDATES EXAM PERFORMANCE => SUCCESSED && FAILED
 * @param dataBody
 * @param callback
 * @returns
 */
export const loadCandidatesExamPerformance = (
  dataBody: FilterParamType | null,
  callback: Function
) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    let _dataBody: FilterParamType = {};
    if (dataBody) {
      if (dataBody.filter) {
        _dataBody.filter = dataBody.filter;
      }
      if (dataBody.limit) {
        _dataBody.limit = dataBody.limit;
      }
      if (dataBody.order) {
        _dataBody.order = dataBody.order;
      }
      if (dataBody.skip) {
        _dataBody.skip = dataBody.skip;
      }
      if (dataBody.sortBy) {
        _dataBody.sortBy = dataBody.sortBy;
      }
    }

    try {
      setAxiosToken();
      const res = dataBody
        ? await axios.post<CandidatesFailedSucceeded[]>(
            `${API.DO_EXAM}/reports/candidate`,
            _dataBody
          )
        : await axios.post<CandidatesFailedSucceeded[]>(
            `${API.DO_EXAM}/reports/candidate`
          );

      dispatch<GetCandidatesExamPerformanceAction>({
        type: ActionTypes.GET_CANDIDATE_EXAMS_PERFOMRANCE,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.error({ code_error: error });
      callback(false, errorToText(error));
    }
  };
};

