import moment, { duration } from "moment";
import React from "react";
import { connect } from "react-redux";
import { Auth } from "../../actions";
import { StoreState } from "../../reducers";

// props for the component
interface AppProps {
  auth: Auth;
  start_time: string;
  duration_min: number;
  timeout: Function;
  returnString?: boolean;
  separator?: string;
  labels?: boolean;
  days?: boolean;
}

interface AppState {
  loading: boolean;
  days: number;
  hours: number;
  mins: number;
  secs: number;
  start_time: string;
  duration: number;
}

class _CountDown extends React.Component<AppProps, AppState> {
  interval: any;
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      days: 0,
      hours: 0,
      mins: 0,
      secs: 0,
      start_time: "",
      duration: 0,
    };
  }

  componentDidMount() {
    // compute the dates and start running the functions
    if (this.props.duration_min === 0 || this.props.start_time === "") {
      return this.setState({
        days: 0,
        hours: 0,
        mins: 0,
        secs: 0,
      });
    } else {
      this.setState({
        duration: this.props.duration_min,
        start_time: this.props.start_time,
      });

      this.setCountdowm(this.props.start_time, this.props.duration_min);
      this.timeOutCounter();
    }
  }

  timeOutCounter = () => {
    this.interval = setInterval(() => {
      this.setCountdowm(this.state.start_time, this.state.duration);
      if (this.state.hours < 0 || this.state.mins < 0 || this.state.secs < 0) {
        this.props.timeout();
        clearInterval(this.interval);
        this.setState({
          days: 0,
          hours: 0,
          mins: 0,
          secs: 0,
        });
      }
    }, 1000);
  };

  componentDidUpdate(prevProps: AppProps, prevState: AppState): void {
    if (
      prevProps.duration_min !== this.props.duration_min ||
      prevProps.start_time !== this.props.start_time
    ) {
      clearInterval(this.interval);
      this.setState({
        duration: this.props.duration_min,
        start_time: this.props.start_time,
      });
      this.timeOutCounter();
    }
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  formatStyle(value: number): string {
    return value <= 9 ? `0${value}` : value + "";
  }

  setCountdowm(start_time: string, duration_min: number) {
    const futureDate = moment(start_time).add(duration_min, "minutes");
    const today = moment();
    const clockDuration = duration(futureDate.diff(today));

    const days = Math.floor(clockDuration.asDays());
    const hours = clockDuration.hours();
    const mins = clockDuration.minutes();
    const secs = clockDuration.seconds();

    this.setState({ days, hours, mins, secs });
  }

  render() {
    if (
      this.props.returnString === undefined ||
      this.props.returnString === false
    ) {
      return (
        <div className="flex items-center">
          {this.props.days !== undefined && this.props.days !== false && (
            <div className="m-0 p-0">
              {this.props.labels !== undefined &&
                this.props.labels !== false && (
                  <span className="m-0 text-xs opacity-80">Days</span>
                )}
              <p className="m-0  timer-count-down -mt-2">
                {this.formatStyle(this.state.days)}
                {this.props.separator === undefined
                  ? ":"
                  : this.props.separator}
              </p>
            </div>
          )}
          <div>
            {this.props.labels !== undefined && this.props.labels !== false && (
              <span className="m-0 text-xs opacity-80">Hours</span>
            )}
            <p className="m-0  timer-count-down -mt-2">
              {this.formatStyle(this.state.hours)}
              {this.props.separator === undefined ? ":" : this.props.separator}
            </p>
          </div>

          <div>
            {this.props.labels !== undefined && this.props.labels !== false && (
              <span className="m-0 text-xs opacity-80">Min</span>
            )}
            <p className="m-0  timer-count-down -mt-2">
              {this.formatStyle(this.state.mins)}
              {this.props.separator === undefined ? ":" : this.props.separator}
            </p>
          </div>

          <div>
            {this.props.labels !== undefined && this.props.labels !== false && (
              <span className="m-0 text-xs opacity-80">Sec</span>
            )}
            <p className="m-0  timer-count-down -mt-2">
              {this.formatStyle(this.state.secs)}
            </p>
          </div>
        </div>
      );
    } else {
      return (
        <span className="timer-count-down">
          {(this.props.days !== undefined || this.props.days !== false) && (
            <span>
              {this.formatStyle(this.state.days)}
              {this.props.separator === undefined ? ":" : this.props.separator}
            </span>
          )}
          <span>{this.formatStyle(this.state.hours)}</span>
          {this.props.separator === undefined ? ":" : this.props.separator}
          <span>{this.formatStyle(this.state.mins)}</span>
          {this.props.separator === undefined ? ":" : this.props.separator}
          <span>{this.formatStyle(this.state.secs)}</span>
        </span>
      );
    }
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const CountDown = connect(mapStateToProps, {})(_CountDown);
