export interface EventInterface {
  event_id: string;
  user_id: string;
  room_id: string;
  exam_id: string;
  position_id: string;
  application_session_id: string;
  event_type_id: string;
  description: string;
  done_by: string;
  time: string;
  createdAt?: string;
  updatedAt?: string;
  group_id: string;
}

export interface LiveServer__Candidate {
  active: boolean;
  done: boolean;
  joined: boolean;
  peer_id: string;
  removed: boolean;
  room_id: string;
  stopped: boolean;
  supervisor: boolean;
  user_id: string;
  added_minutes: number;
}

export interface LiveServer__Room {
  exam_done: boolean;
  group_id: string;
  room_id: string;
  start_exam: boolean;
  start_time: null | string;
  candidates: LiveServer__Candidate[];
  duration: number;
}

export interface LiveServer__Candidate_Details extends LiveServer__Room {
  user: LiveServer__Candidate;
  candidate_timer: {
    start_time: string;
    duration: number;
    minutes: number;
  };
}
