import { ActionTypes } from "../actions";
import { DocumentCategoryActionType } from "../actions/document";
import {
  CandidatesInterface,
  DocumentCategoryInterface,
  DocumentFieldsInterface,
  DocumentSubCategoryInterface,
} from "../shared/interfaces";

export interface DocumentReducerInterface {
  documentCategory: DocumentCategoryInterface[] | null;
  documentSubCategory: DocumentSubCategoryInterface[] | null;
  documentFields: DocumentFieldsInterface[] | null;
  candidatesInInstitute: CandidatesInterface[] | null;
}

// default state
const initialState = {
  documentCategory: null,
  documentSubCategory: null,
  documentFields: null,
  candidatesInInstitute: null,
};

export const documentReducer = (
  state: DocumentReducerInterface = initialState,
  action: DocumentCategoryActionType
) => {
  let index: number;
  switch (action.type) {
    case ActionTypes.GET_DOCUMENT_CATEGORY:
      return {
        ...state,
        documentCategory: action.payload,
      };
    case ActionTypes.CREATE_DOCUMENT_CATEGORY:
      if (!state.documentCategory) {
        state.documentCategory = [];
      }
      return {
        ...state,
        documentCategory: [...state.documentCategory, action.payload],
      };
    case ActionTypes.UPDATE_DOCUMENT_CATEGORY:
      if (!state.documentCategory) {
        state.documentCategory = [];
      }
      index = state.documentCategory.findIndex(
        (doc) =>
          doc.user_detail_category_id === action.payload.user_detail_category_id
      ); //finding index of the item
      if (index >= 0) {
        state.documentCategory[index].title = action.payload.title;
        state.documentCategory[index].description = action.payload.description;
        state.documentCategory[index].fields = action.payload.fields;
      }
      return state;
    case ActionTypes.DELETE_DOCUMENT_CATEGORY:
      if (!state.documentCategory) {
        state.documentCategory = [];
      }
      return {
        ...state,
        documentCategory: state.documentCategory.filter(
          (element) => element.user_detail_category_id !== action.payload
        ),
      };
    case ActionTypes.GET_DOCUMENT_SUB_CATEGORY:
      return {
        ...state,
        documentSubCategory: action.payload,
      };
    case ActionTypes.CREATE_DOCUMENT_SUB_CATEGORY:
      if (!state.documentSubCategory) {
        state.documentSubCategory = [];
      }
      return {
        ...state,
        documentSubCategory: [...state.documentSubCategory, action.payload],
      };
    case ActionTypes.UPDATE_DOCUMENT_SUB_CATEGORY:
      if (!state.documentSubCategory) {
        state.documentSubCategory = [];
      }
      index = state.documentSubCategory.findIndex(
        (doc) =>
          doc.user_detail_subcategory_id ===
          action.payload.user_detail_subcategory_id
      ); //finding index of the item
      if (index >= 0) {
        state.documentSubCategory[index].title = action.payload.title;
        state.documentSubCategory[index].description =
          action.payload.description;
      }
      return state;
    case ActionTypes.DELETE_DOCUMENT_SUB_CATEGORY:
      if (!state.documentSubCategory) {
        state.documentSubCategory = [];
      }
      return {
        ...state,
        documentSubCategory: state.documentSubCategory.filter(
          (element) => element.user_detail_subcategory_id !== action.payload
        ),
      };
    case ActionTypes.GET_DOCUMENT_FIELDS:
      return {
        ...state,
        documentFields: action.payload,
      };
    case ActionTypes.CREATE_DOCUMENT_FIELDS:
      if (!state.documentFields) {
        state.documentFields = [];
      }
      return {
        ...state,
        documentFields: [...state.documentFields, action.payload],
      };
    case ActionTypes.UPDATE_DOCUMENT_FIELDS:
      if (!state.documentFields) {
        state.documentFields = [];
      }
      index = state.documentFields.findIndex(
        (field) => field._id === action.payload._id
      ); //finding index of the item
      if (index >= 0) {
        state.documentFields[index].category_id = action.payload.category_id;
        state.documentFields[index].subcategory_id =
          action.payload.subcategory_id;
        state.documentFields[index].title = action.payload.title;
        state.documentFields[index].description = action.payload.description;
        state.documentFields[index].others = action.payload.others;
        state.documentFields[index].title = action.payload.title;
      }
      return state;
    case ActionTypes.DELETE_DOCUMENT_FIELDS:
      if (!state.documentFields) {
        state.documentFields = [];
      }
      return {
        ...state,
        documentFields: state.documentFields.filter(
          (element) => element._id !== action.payload
        ),
      };
    case ActionTypes.GET_CANDIDATE_IN_INSTITUTE:
      state.candidatesInInstitute = [];
      return {
        ...state,
        candidatesInInstitute: action.payload,
      };
    case ActionTypes.VERIFY_DOCUMENT:
      if (!state.candidatesInInstitute) {
        state.candidatesInInstitute = [];
      }
      let result: CandidatesInterface[] = [];
      const user_index: number = result.findIndex(
        (user) => user.user_id === action.payload.user_id
      );

      state.candidatesInInstitute.forEach((user) => {
        let loopResult: CandidatesInterface = user;
        if (loopResult.user_id === action.payload.user_id) {
          index = loopResult.details.findIndex(
            (doc) => doc.doc_id === action.payload.doc_id
          );

          if (index && index >= 0) {
            loopResult.details[index].validation_status =
              action.payload.validation_status;
            // action.payload.validated_by.validation_status &&
            //   (result[user_index].details[index].validated_by.validation_status =
            //     action.payload.validated_by.validation_status);
            // result[user_index].details[index].validated_by.user_id =
            //   action.payload.validated_by.user_id;
            // result[user_index].details[index].validated_by.comment =
            //   action.payload.validated_by.comment;
            // result[user_index].details[index].validated_by.date = "";
          }
        }
        result.push(loopResult);
      });

      // if (user_index >= 0) {
      //   index = result[user_index].details.findIndex(
      //     (doc) => doc.doc_id === action.payload.doc_id
      //   );

      //   if (index && index >= 0) {
      //     result[user_index].details[index].validation_status =
      //       action.payload.validation_status;
      //     // action.payload.validated_by.validation_status &&
      //     //   (result[user_index].details[index].validated_by.validation_status =
      //     //     action.payload.validated_by.validation_status);
      //     // result[user_index].details[index].validated_by.user_id =
      //     //   action.payload.validated_by.user_id;
      //     // result[user_index].details[index].validated_by.comment =
      //     //   action.payload.validated_by.comment;
      //     // result[user_index].details[index].validated_by.date = "";
      //   }
      // }

      return state;
    default:
      return state;
  }
};
